	module.exports = {"data":{"markdownRemark":{"html":"<p>Getting started is easy. All you need to do is click the <strong>Connect with STRAVA</strong> button in the upper right corner. You will be redirected to Strava to sign in and they will return you, logged in and ready to go.</p>\n<p>Once inside, use the A Race athlete icon in the upper left to open the Menu Drawer to navigate between different features.</p>\n<blockquote>\n<p>Advanced features, such as adding new events, also requires membership to the A Race athlete Strava club (<a href=\"https://www.strava.com/clubs/araceathlete\" target=\"_blank\" rel=\"nofollow\">here</a>).</p>\n</blockquote>","frontmatter":{"title":"How do I get started?","date":"2017-06-02T00:21:00.000Z","path":"/how-do-i-get-started/","parent":"/home/hypersprite/Documents/code/araceathlete-gatsby-material-ui-v1/src/pages/blog-posts/how-do-i-get-started.md absPath of file","tags":"newbie","description":null,"cover":null}}},"pathContext":{"slug":"/blog-posts/how-do-i-get-started/"}}

